      return this.baseInfo[`sence${ua}Url`]
    }
  },
  filters: {
    formatTimeNum(val) {
      // console.log(val, 'filter==================')
      return val > 9 ? val : '0' + val
    },
    formatSaleNumber(v) {
      const nv = Number(v);
      const nvn = (nv / 10000).toFixed(1) + '万'
      return nv <= 9999 ? nv : nvn
    },
    formatPriceInt(v) {
      return v.split('.')[0] + '.';
    },
    formatPriceFloat(v) {
      return v.split('.')[1];
    },
    formatActivityInfo(value) {
      return value.replace(/【(.+?)】/, '')
    },
    withFloat(value) {
      const v = String(value)
      const arr = v.split('.')
      if (arr && arr.length > 1 && arr[1] !== '00' && arr[1] !== '0') {
        return value
      }
      return arr[0]
    },
    formatActivityType (value) {
      let _str
      if (value === 101) {
        // 101 定金尾款预热 距开始时间
        _str = '距活动开始还剩:'
      } else if (value === 100) {
        // 100 定金尾款抢购中 到活动结束时间
        _str = '距活动结束还剩:'
      } else if (value === 81) {
        // 81 预约抢购预约中 到活动结束时间
        _str = '距活动开始还剩:'
      } else if (value === 80) {
        // 80 预约抢购中 到活动结束时间
        _str = '距活动结束还剩:'
      } else if (value === 70) {
        // 70 限时抢购 到活动结束时间
        _str = '距活动结束还剩:'
      } else if (value === 71) {
        // 71 店铺限时抢购预热 到活动开始时间
        _str = '距活动开始还剩:'
      }
      return _str
    },
    attrNameFormat(v) {
      return v.replace(/,/g, ' ')
    }
  },
  methods: {
    /**
     * 跳转拼团规则页
     */
    goGroupRules() {
      this.$router.push('/groupBookingRules');
    },
    fnGoToStore() {
      this.$util.gioTrackWithVariable('MB15622', {
        storeId: this.storeInfo.o2oStoreId + ''
      })
      this.$router.push({
        path: '/shopDetail',
        query: {
          storeId: this.storeInfo.o2oStoreId,
        },
      })
    },
    toggleShowInfo() {
      this.$emit('toggleShowInfo', 0)
    },
    fnExpandNotice() {
      this.expandNotice = !this.expandNotice;
    },
    /** 拼团活动参团 */
    chooseGroup(groupId) {
      this.$emit('toggleShowInfo', groupId)
    },
    /**
     * @description 多规格确定回调
     */
    attrConfirm() {
      switch (this.btnType) {
        case 0:
          // 加购物车
          this.fnAddCart()
          break;
        case 1:
        case 9:
          // 立即购买
          this.$emit('fnGoBuy', 1)
          break;
        case 2:
          // 到货通知
          this.showInfoFlag = !this.showInfoFlag
          this.$emit('fnArrivalNotice')
          break;
        case 3:
          // 马上预定
          this.$emit('bookableBuy')
          break;
        case 4:
          // 全款支付
          this.$emit('fnGoBuy', 1)
          break;
        case 5:
        case 10:
        case 11:
          // 定金支付、拼团
          this.$emit('fnGoBuy', 0)
          break;
        case 8:
          // 立即预约
          this.showInfoFlag = !this.showInfoFlag
          this.$emit('fnReserve')
          break;
        default:
          this.showInfoFlag = !this.showInfoFlag
          break;
      }
    },
    spuConfirm() {
      switch (this.btnType) {
        case 0:
          // 加购物车
          this.fnAddCart()
          break;
        case 1:
        case 9:
          // 立即购买
          this.$emit('fnGoBuy', 1)
          break;
        case 2:
          // 到货通知
          this.showSpuFlag = !this.showSpuFlag
          this.$emit('fnArrivalNotice')
          break;
        case 3:
          // 马上预定
          this.$emit('bookableBuy')
          break;
        case 4:
          // 全款支付
          this.$emit('fnGoBuy', 1)
          break;
        case 5:
        case 10:
        case 11:
          // 定金支付、拼团
          this.$emit('fnGoBuy', 0)
          break;
        case 8:
          // 立即预约
          this.showSpuFlag = !this.showSpuFlag
          this.$emit('fnReserve')
          break;
        default:
          this.showSpuFlag = !this.showSpuFlag
          break;
      }
    },
    fnArrivalNotice() {
      // 到货通知
      if (this.spuInfo) {
        this.showSpuFlag = !this.showSpuFlag
      } else {
        this.showInfoFlag = !this.showInfoFlag
      }
      this.$emit('fnArrivalNotice')
    },
    buy() {
      // 立即购买
      this.$emit('fnGoBuy', 1)
    },
    depositBuy() {
      // 定金支付
      this.$emit('fnGoBuy', 0)
    },
    fullBuy() {
      // 全款支付
      this.$emit('fnGoBuy', 1)
    },
    /**
     * 收藏
    */
    fnCollect() {
      this.$emit('fnCollect')
    },
    /**
     * 打开地址选择弹窗
    */
    openAddr() {
      // if (this.sgStockInfo.skuDisplayState === 4) {
      //   // 私域情况下 超出门店配送范围 锁定 省市区级联只能选配送范围内的地址
      //   if (this.storeInfo) {
      //     this.addrStoreId = this.storeInfo.o2oStoreId
      //   }
      // } else {
      //   this.addrStoreId = ''
      // }
      this.addrStoreId = this.visitStore || ''
      if (!this.addrLoad) {
        lazyAddrList().then(() => {
          this.addrLoad = true;
          this.$nextTick(() => {
            if (this.$refs.addrList) {
              this.$refs.addrList.openAddr()
            }
          })
        })
        return;
      }
      this.$refs.addrList.openAddr()
    },
    selectTitle() {
      this.$emit('titleSelectOption', true)
    },
    countdownFinish() {
      this.$emit('countdownFinish')
    },
    attrNameChange(v) {
      this.$emit('attrNameChange', v)
    },
    fnChangeProductNumber(value) {
      this.stepperNum = value;
      this.$emit('fnChangeProductNumber', value)
    },
    changeGoodsNumber(value) {
      this.stepperNum = value;
      this.$emit('changeGoodsNumber', value)
    },
    fnShowVoucher () {
      this.showVoucherFlag = !this.showVoucherFlag
    },
    fnShowInfo (value) {
      // this.btnType = value // 0:加入购物车 1:立即购买
      // 如果spu信息存在 走虚拟spu弹窗展示逻辑 否则走多规格逻辑
      if (this.spuInfo) {
        this.showSpuFlag = !this.showSpuFlag
        this.$emit('titleSelectOption', !this.showSpuFlag)
      } else {
        this.showInfoFlag = !this.showInfoFlag
        this.$emit('titleSelectOption', !this.showInfoFlag)
      }
    },
    showInfo() {
      // 重置操作按钮类型
      this.$emit('resetBtnType')
      this.showInfoFlag = !this.showInfoFlag
    },
    changeLocation (changeModal) {
      this.showChooseLoaction = changeModal
    },
    /**
     * @description 点击选择所在地区 确认关闭回调
     */
    handleLocation (data) {
      // this.showChooseLoaction = false
      // console.log('位置选择:===>', data)
      // this.$store.commit('setLocationInfo', {
      //   ...data,
      //   // type: 'userSelected'
      // })
      let gioParam = {
        storeId: this.storeInfo && this.storeInfo.o2oStoreId ? this.storeInfo.o2oStoreId + '' : 'null',
        deliverStreet: 'null'
      }
      // if (this.storeInfo && this.storeInfo.o2oStoreId) {
      //   gioParam.storeId = (this.storeInfo.o2oStoreId + '') || 'null'
      // }
      let regionArr = this.$util.strJSONParse(localStorage.getItem('location'), {}).region.split(' ')
      if (regionArr.length === 4) {
        gioParam.deliverStreet = regionArr[3] ? regionArr[3] + '' : 'null'
      }
      this.$util.gioTrackWithVariable('MB15619', gioParam)
      this.$emit('handleLocation')
      try {
        gio('track', 'ProView', {
          deliverStreet: this.$util.strJSONParse(localStorage.getItem('location'), {}).township,
          deliverDistrict: this.$util.strJSONParse(localStorage.getItem('location'), {}).district,
          deliverCity: this.$util.strJSONParse(localStorage.getItem('location'), {}).city,
          deliverProvince: this.$util.strJSONParse(localStorage.getItem('location'), {}).province
        })
      } catch (error) {

      }
    },
    fnSelectAttribute (data) {
      // // 多规格 规格未选完 不刷新
      // if (!data.price) return
      this.stepperNum = this.stepperNum ? this.stepperNum : (this.newStockNum > 0 ? 1 : 0);
      this.$emit('fnSelectAttribute', data, this.stepperNum)
    },
    /**
     * @description 切换选择 spu产品
    */
    fnSelectSpu(obj) {
      this.$emit('fnSelectSpu', obj)
    },
    fnAddCart () {
      this.$emit('fnAddCart')
    },
    fnGoBuy () {
      this.$emit('fnGoBuy')
    },
    receiveCoupon (data) {
      this.$emit('receiveCoupon', data)
    },
    goBaitiao () {
      this.$emit('goBaitiao')
    },
    /**
     * @description 服务说明
     */
    fnOpenDesc () {
      this.showDescription = true
      this.$emit('getDes')
    },
    /**
     * @description 设计师推荐方案
     */
    fnOpenProgram () {
      this.$util.gioTrackWithVariable('MB15624')
      let sceneUrl = this.sceneUrl
      if (!sceneUrl.includes('?')) {
        sceneUrl += '?'
      } else {
        sceneUrl += '&'
      }
      sceneUrl += `userId=${this.$store.state.ucId ? this.$store.state.ucId : ''}&userAccessToken=${this.$store.getters.getUserCenterToken}`
      this.$store.dispatch('goPageByType', {
        type: jumpType,
        params: this.$util.injectISOAddressCodeToURL(sceneUrl, this.$store.getters.getlocationInfo),
      })
      // this.$router.push({
      //   path: 'itSoluteList',
      //   query: {
      //     productId: this.productId,
      //   },
      // })
    },
    /**
     * @description 展示白条方案
     */
    fnOpenIouc () {
      this.$emit('fetchIoucCost')
    },
    /**
     * @description 3D展示
     */
    fnOpen3D () {
      this.$util.openNewWindow(this.products.vrUrl, true)
    },
    /**
     * @description 跳转以旧换新页面
    */
    toOldForNew() {
      // 判断三翼鸟app与智家环境跳转不同路径
      if (this.$util.isUplus()) {
        // 正式环境地址
        this.$util.openNewWindow(`mpaas://vipValueCenter?needAuthLogin=1#/readyValue`, true, true);
        // // 测试环境地址
        // this.$util.openNewWindow(`https://hzytest.haier.com/cn/vipcode/readyValue?resource=uhome&needAuthLogin=1&container_type=3`, true, true, false);
      } else if (this.$util.isSyBird()) {
        // 正式环境地址
        this.$util.openNewWindow(`https://hzy.haier.com/cn/distcord/readyValue?resource=uhome&needAuthLogin=1&container_type=3`, true, true, false);
        // // 测试环境地址
        // this.$util.openNewWindow(`https://hzytest.haier.com/cn/vipcode/readyValue?resource=uhome&needAuthLogin=1&container_type=3`, true, true, false);
      }
    },
    sortList(arr) {
      let len = arr.length;
      for (var i = 0; i < len; i++) {
        for (var j = 0; j < len - 1 - i; j++) {
          if (arr[j].tagOrder > arr[j + 1].tagOrder) {
            var temp = arr[j + 1]
            arr[j + 1] = arr[j]
            arr[j] = temp
          }
        }
      }
      return arr
    },
    // changePriceRange(val) {
    //   this.priceValueRange = val
    //   // 规格是否选中状态判断 ，有价格区间 说明无规则选中，无则代表有规则选中
    //   if (val) {
    //     this.$emit('attrSelectChanged', false)
    //   } else {
    //     this.$emit('attrSelectChanged', true)
    //   }
    // },
    changePriceValue(val) {
      this.priceValue = val
    }
  },
  watch: {
    ioucInfo: function (val) {
      this.showIoucAction = true
    },
    // showIoucAction: function(val) {
    //   if (val) {
    //     this.$modalHelper.afterOpen()
    //   } else {
    //     this.$modalHelper.beforeClose()
    //   }
    // },
    showVoucherFlag (val) {
      this.$parent.scrollDisable = val
    },
    sgStockInfo: {
      deep: true,
      immediate: true,
      handler(newVal, oldVal) {
        this.newStockNum = newVal.stockNum;
        // this.$set(this.sgStockInfo, 'stockNum', newVal.stockNum)
      }
    }
  },
}
</script>
