<template>
  <div class="coupon-item">
    <div class="content-wrapper">
      <div class="value-wrapper">
        <div class="value-label">
          <coupon-value-label textColor="#ffffff" :couponValue='couponData.couponValue' :couponType='couponData.couponType'></coupon-value-label>
          <p class="value-tips">{{couponData.description}}</p>
        </div>
        <!-- <img class="value-tag" :src="`${$cdnImageUrl}/voucher/quan_pingtai.png`" alt=""> -->
        <img v-if="couponData.couponUseType == 'SHOP'" class="value-tag" :src="`${$cdnImageUrl}/voucher/quan_dianpu.png`" alt="">
        <img v-if="couponData.couponUseType == 'ORDER'" class="value-tag value-tag-small" :src="`${$cdnImageUrl}/voucher/quan_ka.png`" alt="">
        <img v-if="couponData.marking == 'sheng'" class="value-tag-quan" :src="`${$cdnImageUrl}/goodsDetail/quan_sheng.png`" alt="">
        <img v-if="couponData.marking == 'cou'" class="value-tag-quan" :src="`${$cdnImageUrl}/goodsDetail/quan_cou.png`" alt="">
        <img class="value-label-bg" :src="`${$cdnImageUrl}/voucher/coupon_left.png`" alt="">
        <!-- <span class="val-icon">￥</span>
        <span class="val-num" :class="{bigVal:isBigVal}">{{couponData.couponValue}}</span> -->
      </div>
      <div class="info-wrapper">
        <div class="info-body">
          <div class="info">
            <p class="info-text">
              <span v-if="couponData.scope==='PRODUCT'" class="singleItem">单品</span>
              <span v-if="couponData.scope==='ORDER'" class="singleItem">订单</span>
              {{couponData.description}}
            </p>
            <p class="info-text">{{couponData.couponTemplateName}}</p>
            <p class="info-text" v-if="couponData.startTime">{{couponData.startTime | formatDateEndOfDay}} — {{couponData.endTime | formatDateEndOfDay}}</p>
            <p class="info-text" v-else>{{couponData.validityPeriod}}</p>
          </div>
          <div class="receive">
            <div v-if="couponData.displayType==='1'" class="not-received">
              <span class="receive-btn" @click="receive">点击领取</span>
            </div>
            <div v-if="couponData.displayType==='2'" class="received">
              <img :src="`${$cdnImageUrl}/goodsDetail/received.png`" alt="">
            </div>
            <div v-if="couponData.displayType==='5'" class="sold-out">
              <img :src="`${$cdnImageUrl}/voucher/voucher-center-yiqiangguang.svg`" class="sold-out-img">
            </div>
          </div>
        </div>
        <img class="info-warp-bg" :src="`${$cdnImageUrl}/voucher/coupon_right.png`" alt="">
      </div>
    </div>

    <!-- <img class="type-icon" :src="iconSrc" alt="">
    <img class="new-coupon-bg" :src="`${$cdnImageUrl}/shop/yhq-new-coupon-bg.png`" /> -->
  </div>
</template>

<script>
import couponValueLabel from '@/components/couponValueLabel'

export default {
  components: { couponValueLabel },
  props: {
    couponData: Object
  },
  data () {
    return {}
  },
  created () {},
  mounted () {},
  beforeDestroy () {},
  methods: {
    receive () {
      this.$util.gioTrackWithVariable('MB15621', {
        ad_coupon_id: this.couponData.couponTemplateCode ? this.couponData.couponTemplateCode + '' : 'null'
      })
      console.log('this.couponData', this.couponData)
      const time = {
        startTime: this.couponData.startTime ? this.couponData.startTime.split(' ')[0] : '',
        endTime: this.couponData.endTime ? this.couponData.endTime.split(' ')[0] : ''
      }
      this.$emit('receive', time)
    }
  },
  computed: {

    isBigVal () {
      return this.couponData.couponValue > 1000
    },
    couponIcon () {
      const _couponIcon = {
        ORDER: `${this.$cdnImageUrl}/shop/icon-coupon-card.png`,
        SCENE: `${this.$cdnImageUrl}/shop/icon-coupon-scene.png`,
        // ORDER: `${this.$cdnImageUrl}/shop/icon-coupon-plat.png`,
        SHOP: `${this.$cdnImageUrl}/shop/icon-coupon-shop.png`
      }
      return _couponIcon
    },
    iconSrc () {
      let type = this.couponData.couponUseType

      if (this.couponData.applyType) type = 'SCENE'

      return this.couponIcon[type] || type
    }
  },
  watch: {}
}
</script>

