<script>
import couponItem from './coupon-item.vue'
export default {
  components: { couponItem },
  props: ['voucherList', 'finished', 'getGoodsUseCouponInfo', 'rightsList'],
  data() {
    return {
      loading: false,
      rightsFold: true
    }
  },
  methods: {
    fnShowVoucher() {
      this.$emit('fnShowVoucher')
    },
    fnReceiveVoucher(data, index, e) {
      // 判断是否登录 没登录跳转登录页面
      if (!this.$store.getters.getLoginStatus) {
        this.$store.dispatch('doLogin')
        return
      }
      // 已领取
      if (data.displayType === 2) {
        return
      }
      console.log(data, index, e, 'fnReceiveVoucher')
      this.receiveCoupon({
        activityCode: data.activityCode,
        couponTemplateCode: data.couponTemplateCode,
        sourceChannel: data.sourceChannel,
        index: index,
        ...data,
        ...e
      })
    },
    receiveCoupon(data) {
      this.$emit('receiveCoupon', data)
    },
  },
  computed: {
    rightsData() {
      let rightsData = {
        rightsTopList: [],
        rightsBottomList: []
      }

      this.rightsList.forEach((right, index) => {
        if (!(this.rightsFold && index > 4)) {
          if (right.isShare) {
            rightsData.rightsTopList.push(right)
          } else {
            rightsData.rightsBottomList.push(right)
          }
        }
      });

      return rightsData
    }
  }
}
</script>
