<template>
  <div class="container">
    <div @click="fnShowInfo" class="goods-info-info-wrap">
      <div @click.stop class="content">
        <img @click="fnShowInfo" alt class="close" :src="`${$cdnImageUrl}/common/control-close.png`" />
        <div class="content-wrap">
          <div class="head">
            <div class="head-box">
              <div class="left">
                <div class="img-box" @click="fnPreviewImage()">
                  <img alt :src="images ? images[0] : '' | imageToHttps" />
                </div>
              </div>
              <div class="right">
                <div class="jq">
                  <template v-if="btnType === 4 || btnType === 9">
                    <span v-if="priceInfo.originPrice || priceInfo.originPrice === 0">
                      <span class="yuan">￥ </span>
                      <span class="rmb yuan-int">{{ priceInfo.originPrice|formatPrice | formatPriceInt }}</span>
                      <span class="dot-after" >.{{ priceInfo.originPrice |formatPrice | formatPriceFloat}}</span>
                    </span>
                  </template>
                  <template v-else>
                    <span v-if="priceInfo.actualPrice || priceInfo.actualPrice === 0">
                      <span class="yuan">￥ </span>
                      <span class="rmb yuan-int">{{ priceInfo.actualPrice|formatPrice | formatPriceInt }}</span>
                      <span class="dot-after" >.{{ priceInfo.actualPrice |formatPrice | formatPriceFloat}}</span>
                    </span>
                  </template>
                  </div>
                <p class="price-quan" v-if="this.baseInfo.showCoupons && getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type=='sheng'">预计到手价{{getGoodsUseCouponInfo.price.toFixed(2)}}</p>
                <div class="kc" v-if="btnType !==3 && (priceInfo.actualPrice || priceInfo.actualPrice === 0) && !sgStockInfo.bookable">库存{{ showStockNum }}件</div>
                <div class="yx" v-if="spuSelect">{{ spuSelect.skuShortName }}</div>
              </div>
            </div>
          </div>
          <div class="scroll-view goods-info-info-wrap-content">
            <div class="content-detail">
              <div :key="index" class="content-detail-inner" v-for="(item, index) in spuInfo.spuSpecsList" :class="index === Object.keys(attrsMap).length-1  ? '__last' : ''">
                <div class="content-detail-inner-tit">{{ item.specsName }} </div>
                <div class="content-detail-inner-list">
                  <div
                    :class="{ active: +v.productId === +productId}"
                    :key="v.id"
                    @click="handleSelectedSpu(v)"
                    class="content-detail-inner-item"
                    v-for="v in item.spuSkuList"
                  >
                    {{ v.skuShortName}}
                  </div>
                </div>
              </div>
              <div class="content-detail-inner1">
                <div class="left">数量<span class="limit" v-if="activityLimitFlag">限购{{activityInfo.purchase.purchaseNum}}件,还可购买{{activityInfo.purchase.canBuyNum}}件</span></div>
                <div class="right">
                <div class="customer-stepper">
                  <div :class="{'reduce-icon-gray':goodsNum <= 1,'reduce-icon':goodsNum > 1}" @click="reduce"></div>
                  <input type="number" v-model="goodsNum" class="num-input" :disabled="disabsedInput" :min="minGoodsNum" :max="maxGoodsNum" @change="fnChangeProductNumber" />
                  <div :class="{
                    'plus-icon-gray': plusIconGray,
                    'plus-icon':!plusIconGray }" @click="plus"></div>
                </div>
                </div>
              </div>
              <!-- 白条 -->
              <div class="iouc-container" >
                <div class="header">
                  <div class="title"><span class="title-text">白条</span><span class="title-info">（打白条请到支付页面）</span></div>
                  <div class="tip"><div class="tip-icon"></div><span class="tip-text">6期免息</span></div>
                </div>
                <div class="iouc-content" v-if="ioucInfo && ioucInfo.costInfo">
                  <div class="iouc-item"  v-for="(item, index) in ioucInfo.costInfo" :key="index">
                    <span class="price">￥{{item.eachPrinAndFee}} &times; {{item.num}}期</span>
                    <span class="fee">{{item.fee}}服务费</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
           <div class="btn-container" :class="{'disabled':notSaleStatus || activityDisabledFlag}">
            <template v-if="notSaleStatus">
              <div class="btn-List-tow" >
                <div class="confirm-btn yellow"><span>加入购物车</span></div>
                <div class="confirm-btn" :class="{'orange': !(activityInfo && activityInfo.activityType === 70) && getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type == 'sheng'}">
                   <span>{{checkCouponIsAllGet ? '立即购买' : '领券购买'}}</span>
                   <h6 v-if="getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type=='sheng'">到手价{{getGoodsUseCouponInfo.price.toFixed(2)}}</h6>
                </div>
              </div>
            </template>
            <template v-else-if="isShowNoStockBtn">
                  <div class="confirm-btn yellow" @click="fnArrivalNotice"><span>到货通知</span></div>
            </template>
            <template v-else>
              <template v-if="btnType===11">
                <div class="btn-List-tow">
                  <div @click="fullBuy" class="confirm-btn">
                    <div class="origin-price">
                      <span>¥</span>{{ priceInfo.minOriginPrice || priceInfo.originPrice || 0 | formatPrice | formatPriceInt }}<span class="right">.{{ priceInfo.minOriginPrice || priceInfo.originPrice || 0 | formatPrice | formatPriceFloat}}</span>
                    </div>
                    <span class="text">原价购买</span>
                  </div>
                  <div v-if="hasActivityStock" @click="confirm" class="confirm-btn" style="background-color: #FF5252;">
                    <div class="origin-price">
                      <span>¥</span>{{(priceInfo.minPrice || priceInfo.actualPrice || 0) | formatPrice | formatPriceInt}}<span class="right">.{{(priceInfo.minPrice || priceInfo.actualPrice || 0) | formatPrice | formatPriceFloat}}</span>
                    </div>
                    <span class="text">拼团购买</span>
                  </div>
                  <template v-else>
                        <div class="confirm-btn yellow" @click="fnArrivalNotice"><span>到货通知</span></div>
                  </template>
                </div>
              </template>
              <!-- 确认按钮 -->
              <div class="confirm-btn" v-if="btnType===0 || btnType===1 || btnType === 9 || btnType === 10" @click="confirm"><span>确定</span></div>
              <div class="confirm-btn yellow" v-if="btnType===2 && (sgStockInfo && !sgStockInfo.hasStock) && !sgStockInfo.bookable" @click="confirm"><span>到货通知</span></div>
              <div class="confirm-btn green" v-if="btnType===3" @click="confirm"><span>马上预定</span></div>
              <div class="confirm-btn zise" v-if="btnType===8" @click="confirm"><span>立即预约</span></div>
              <div class="confirm-btn full" v-if="btnType===4" @click="confirm"><span>原价购买</span></div>
              <div class="confirm-btn zise" v-if="btnType===5" @click="confirm"><span><span v-if="activityInfo && activityInfo.deposit !== 1">￥{{ priceInfo.actualPrice ? priceInfo.actualPrice : 0 | formatPrice }}</span>{{activityInfo && activityInfo.deposit === 1 ?'支付订金' : '订金支付'}}</span></div>
              <div class="btn-List-tow" v-if="btnType===6">
                <div class="confirm-btn yellow" @click="addCart"><span>加入购物车</span></div>
                <div class="confirm-btn" :class="{'orange': !(activityInfo && activityInfo.activityType === 70) && getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type == 'sheng'}" @click="buy">
                  <span>{{ activityInfo && activityInfo.activityType === 70 ? '立即抢购' : checkCouponIsAllGet ? '立即购买' : '领券购买' }}</span>
                  <h6 v-if="getGoodsUseCouponInfo && getGoodsUseCouponInfo.voucherLen && getGoodsUseCouponInfo.type=='sheng'">到手价{{getGoodsUseCouponInfo.price.toFixed(2)}}</h6>
                </div>
              </div>
              <div class="btn-List-tow" v-if="btnType===7">
                <div class="confirm-btn full" @click="fullBuy"><span>原价购买</span></div>
                <div class="confirm-btn zise" @click="depositBuy"><span><span v-if="activityInfo && activityInfo.deposit !== 1">￥{{ priceInfo.actualPrice ? priceInfo.actualPrice : 0 | formatPrice }}</span>{{activityInfo && activityInfo.deposit === 1 ?'支付订金' : '订金支付'}}</span></div>
              </div>
            </template>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ImagePreview } from 'vant'
export default {
  props: ['sgAttribute', 'sgStoreAttribute', 'checkCouponIsAllGet', 'getGoodsUseCouponInfo', 'checkedAttr', 'sgStockInfo', 'stepperNum', 'ioucInfo', 'btnType', 'hasSubProduct', 'activityInfo', 'attrLength', 'priceInfo', 'productId', 'spuInfo', 'swiperImgs', 'productNumber', 'notSaleStatus', 'products', 'baseInfo'],
  name: 'spu-modal',
  data() {
    return {
      goodsNum: 1,
      productInfo: {}, // 根据规格筛选出的商品信息
      checkedAttrIdsArr: [], // 选择的规格ID
      checkedAttrIdsStr: '', // 选择的规格ID
      attrName: '', // 规格组合名字
      filterAttrs: [], // 可选规格组合筛选后的数据
      originAttrs: [], // 可选规格组合原始数据
      attrsMap: [], // 规格参数，添加selected,enable字段
      priceArr: [], // 价格
      newSgStockInfo: {},
      // priceRange: '', // 多规格时未选中状态展示价格区间
      maxPrice: 0, // 最高价格
      minPrice: 0, // 最高价格
      checkedAttrTypeArr: [], // 已选规格类型
      spuSelect: null, // 已选 spu
      images: null, // 图片预览数据源
      limitNum: 500

    }
  },
  mounted() {
  },
  computed: {
    minGoodsNum() {
      return this.activityLimitFlag ? this.activityInfo.purchase.canBuyNum : this.productInfo && !this.productInfo.bookable && this.productInfo.stockNum === 0 ? 0 : 1
    },
    maxGoodsNum() {
      // return this.activityLimitFlag ? this.activityInfo.purchase.canBuyNum : this.products.isNoLimitStockProduct ? this.limitNum : this.productInfo ? this.productInfo.stockNum > this.limitNum ? this.limitNum : this.productInfo.stockNum : 1
      return this.activityLimitFlag ? this.activityInfo.purchase.canBuyNum : this.limitNum
    },
    disabsedInput() {
      return this.activityDisabledFlag || (this.productInfo && this.productInfo.stockNum === 0 && !this.sgStockInfo.bookable && !this.products.isNoLimitStockProduct)
    },
    plusIconGray () {
      // 如果在限购活动中goodsNum >= 可购买数量时 加号变灰
      if (this.activityLimitFlag) {
        return this.goodsNum >= this.activityInfo.purchase.canBuyNum
      }
      return this.goodsNum >= this.limitNum
    },
    /**
     * @description 限购活动标志
     */
    activityLimitFlag() {
      return this.btnType !== 4 && this.btnType !== 9 && this.activityInfo && this.activityInfo.purchase && this.activityInfo.purchase.canBuyNum !== null && this.activityInfo.purchase.purchaseNum !== null
    },
    /**
     * @description 限购活动中可购买数量是否为0
     */
    activityDisabledFlag() {
      return this.activityLimitFlag && this.activityInfo.purchase.canBuyNum === 0
    },
    // 显示库存
    showStockNum() {
      let num = 0;
      if ([10, 11].indexOf(this.btnType) !== -1 && this.activityInfo && (this.activityInfo.stock || this.activityInfo.stock === 0)) {
        num = this.activityInfo.stock
      } else {
        num = this.sgStockInfo ? this.sgStockInfo.stockNum : 0
      }
      return num;
    },
    // 是否有活动库存
    hasActivityStock() {
      return (this.activityInfo && this.activityInfo.stock > 0) || (this.activityInfo.stock === null && this.sgStockInfo && this.sgStockInfo.hasStock);
    },
    // 是否显示到货通知
    isShowNoStockBtn() {
      return (this.activityInfo && this.activityInfo.stock === 0 && [10, 11].indexOf(this.btnType) !== -1 && this.sgStockInfo && !this.sgStockInfo.hasStock) || (this.sgStockInfo && !this.sgStockInfo.hasStock && !this.parentBookable && !this.sgStockInfo.bookable && !this.notSaleStatus);
    }
  },
  filters: {
    formatPriceInt(v) {
      return v.split('.')[0];
    },
    formatPriceFloat(v) {
      return v.split('.')[1];
    }
  },
  watch: {
    activityLimitFlag() {
      this.fnChangeProductNumber()
    },
    // DSG-25627
    activityDisabledFlag: {
      handler(nv) {
        if (nv) {
          this.goodsNum = 0
        } else {
          this.goodsNum = 1
        }
      },
      immediate: true
    },
    productNumber: {
      handler(newVal, oldVal) {
        if (this.goodsNum !== Number(newVal)) {
          this.goodsNum = newVal
        }
      }
    },
    sgStockInfo: {
      deep: true,
      handler(newVal, oldVal) {
        this.productInfo.stockNum = newVal.stockNum;
        // 切换商品的时候也要加上限购活动的逻辑
        if (this.activityInfo && this.activityInfo.stock > 0) {
          this.limitNum = this.activityInfo.stock
        } else if ((newVal.stockNum > 0 || this.sgStockInfo.bookable || this.products.isNoLimitStockProduct) && !this.activityDisabledFlag) {
          if (this.sgStockInfo.bookable || this.products.isNoLimitStockProduct) {
            // 预定商品/不校验库存商品 不需要校验库存，购买数量不计算库存数
            this.limitNum = Math.min(this.sgStockInfo.limitNum || Infinity, 500)
          } else if (this.activityInfo && this.activityInfo.stock > 0) {
            this.limitNum = this.activityInfo.stock
          } else {
            this.limitNum = Math.min(this.sgStockInfo.stockNum, this.sgStockInfo.limitNum || Infinity, 500)
          }
          if (newVal.stockNum !== oldVal.stockNum) {
            this.fnChangeProductNumber()
          }
        } else {
          this.goodsNum = 0
        }
      }
    },
    spuInfo: {
      deep: true,
      immediate: true,
      handler(newVal, oldVal) {
        this.initSelectSpu(newVal)
      }
    },
    swiperImgs: {
      deep: true,
      immediate: true,
      handler(newVal, oldVal) {
        this.images = []
        newVal.forEach(i => {
          if (i.img || i.baseImage) {
            this.images.push(i.img || i.baseImage)
          } else {
            this.images.push(i)
          }
        })
      }
    }
  },
  methods: {
    initSelectSpu(v) {
      v.spuSpecsList.forEach(i => {
        i.spuSkuList.forEach(s => {
          console.log('this.productId:', this.productId)
          if (s.productId === Number(this.productId)) {
            this.spuSelect = s
          }
        })
      })
    },
    /**
     * @description 到货通知
     */
    fnArrivalNotice() {
      if (this.activityDisabledFlag) return
      this.$emit('fnArrivalNotice')
    },
    /**
     * @description  数量减操作
     */
    reduce() {
      if (this.activityDisabledFlag) return
      this.goodsNum = this.goodsNum === 0 ? 0 : this.goodsNum - 1
      this.fnChangeProductNumber()
    },
    /**
     * @description  数量加操作
     */
    plus() {
      if (this.activityLimitFlag && this.plusIconGray) return

      this.goodsNum = this.goodsNum >= 500 ? 500 : this.goodsNum + 1
      this.fnChangeProductNumber()
    },
    /**
     * @description  选择某个spu规格
     * @param {string} productId 所选规格对应的产品id
     */
    handleSelectedSpu(obj) {
      this.spuSelect = obj
      this.$emit('fnSelectSpu', obj)
    },
    fnShowInfo() {
      this.$emit('fnShowInfo')
    },
    fnPreviewImage(pic) {
      let images = this.images
      ImagePreview({
        images,
        startPosition: 0,
        onClose() {
          // do something
        },
      })
    },
    fnChangeProductNumber(value) {
      this.checkNumber()
      this.$emit('fnChangeProductNumber', this.goodsNum)
    },
    /**
     * @description  检查输入数量是否合法
     */
    checkNumber() {
      if (this.goodsNum < 1 && !this.activityDisabledFlag) {
        this.goodsNum = 1
        return
      }
      // DSG-25621
      if (this.activityLimitFlag) {
        this.goodsNum = this.goodsNum > this.maxGoodsNum ? this.maxGoodsNum : this.goodsNum
      } else {
        if (this.activityInfo && this.activityInfo.stock > 0) {
          if (this.goodsNum > this.activityInfo.stock) {
            this.goodsNum = this.activityInfo.stock
          }
        } else if (this.sgStockInfo && this.sgStockInfo.limitNum) {
          if (this.sgStockInfo.bookable || this.products.isNoLimitStockProduct) {
            const limitNum = Math.min(this.sgStockInfo.limitNum || Infinity, 500)
            if (this.goodsNum > limitNum) {
              this.goodsNum = limitNum
              this.$toast(`为了您的购物体验，当前商品一次购买数量不超过${limitNum}`)
            }
          } else {
            const limitNum = Math.min(this.sgStockInfo.stockNum, this.sgStockInfo.limitNum || Infinity, 500)
            if (this.goodsNum > limitNum) {
              this.goodsNum = limitNum
              this.$toast(`为了您的购物体验，当前商品一次购买数量不超过${limitNum}`)
            }
          }
        } else {
          if (this.sgStockInfo.bookable || this.products.isNoLimitStockProduct) {
            const limitNum = 500
            if (this.goodsNum > limitNum) {
              this.goodsNum = limitNum
            }
          } else {
            const limitNum = Math.min(this.sgStockInfo.stockNum, 500)
            if (this.goodsNum > limitNum) {
              this.goodsNum = limitNum
            }
          }
        }
        // if (!this.sgStockInfo.limitNum && this.products.isNoLimitStockProduct) {
        //   if (this.goodsNum > 500) this.goodsNum = 500
        // } else {
        // if (this.goodsNum > this.limitNum) {
        //   this.goodsNum = this.limitNum
        //   if (this.sgStockInfo.limitNum && this.sgStockInfo.limitNum < this.sgStockInfo.stockNum) {
        //     this.$toast(`为了您的购物体验，当前商品一次购买数量不超过${this.sgStockInfo.limitNum}`)
        //   }
        // }
        // }
      }
    },
    fnAddCart() {
      if (this.activityDisabledFlag) return
      this.$emit('fnAddCart')
    },
    fnGoBuy() {
      if (this.activityDisabledFlag) return
      this.$emit('fnGoBuy')
    },
    /**
     * @description  确定
     */
    confirm() {
      if (this.activityDisabledFlag) return
      if ((this.goodsNum > 0 && this.spuSelect) || this.btnType === 2) {
        this.$emit('attrConfirm')
      }
    },
    /**
     * @description  加购物车
     */
    addCart() {
      if (this.activityDisabledFlag) return
      if (this.goodsNum > 0 && this.spuSelect) {
        this.$emit('addCart')
      }
    },
    /**
     * @description  立即购买
     */
    buy() {
      if (this.activityDisabledFlag) return
      if (this.goodsNum > 0 && this.spuSelect) {
        this.$emit('buy')
      }
    },
    depositBuy() {
      if (this.activityDisabledFlag) return
      if (this.goodsNum > 0 && this.spuSelect) {
        this.$emit('depositBuy')
      }
    },
    fullBuy() {
      if (this.activityDisabledFlag) return
      if (this.goodsNum > 0 && this.spuSelect) {
        this.$emit('fullBuy')
      }
    },
  },
}
</script>
